.image-container i:last-child {
    position: absolute;
    z-index: 9;
    top:0;
    right: 0;
    padding: 6px 2px;
    color: rgb(104, 231, 104);
    cursor:pointer;
    
}

.image-container i:nth-child(2) {
    position: absolute;
    top:0;
    z-index: 9;
    left:0;
    padding: 6px 2px;
    /* color: rgb(231, 80, 80); */
    cursor:pointer;
}

